@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap);
:root {
  --primary-color: #007AFF;
  --secondary-color: #FF9500;
  --background-color: #F2F2F7;
  --text-color: #1D1D1F;
  --card-color: #FFFFFF;
  --shadow-color: rgba(0, 0, 0, 0.1);
  --green-color: #4CAF50;
  --amber-color: #FFA000;
  --red-color: #FF5722;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Inter', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  line-height: 1.6;
}

.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.main-title {
  font-size: 2.5rem;
  font-weight: 700;
  text-align: center;
  color: var(--text-color);
  margin: 2rem 0;
}

.intro {
  font-size: 1.1rem;
  text-align: center;
  margin-bottom: 2rem;
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.process-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 2rem;
  gap: 2rem;
}

.breadcrumbs {
  margin-bottom: 1rem;
  font-size: 0.9rem;
}

.breadcrumbs a {
  color: var(--primary-color);
  text-decoration: none;
}

.breadcrumbs a:hover {
  text-decoration: underline;
}

@media (max-width: 768px) {
  .process-grid {
    grid-template-columns: 1fr;
  }

  .main-title {
    font-size: 2rem;
  }

  .intro {
    font-size: 1rem;
  }
}

.search-bar {
  display: flex;
  max-width: 600px;
  margin: 0 auto 2rem;
}

.search-bar input {
  flex-grow: 1;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 8px 0 0 8px;
  outline: none;
  box-shadow: 0 2px 10px var(--shadow-color);
}

.search-bar button {
  background-color: var(--primary-color);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  border-radius: 0 8px 8px 0;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.search-bar button:hover {
  background-color: #0056b3;
}

@media (max-width: 768px) {
  .search-bar {
    flex-direction: column;
  }

  .search-bar input,
  .search-bar button {
    width: 100%;
    border-radius: 8px;
  }

  .search-bar input {
    margin-bottom: 0.5rem;
  }
}

.process-card {
  background-color: var(--card-color);
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 4px 15px var(--shadow-color);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.process-icon {
  font-size: 2.5rem;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

.process-card h3 {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.process-description {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 1rem;
}

.pain-points-indicator {
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: white;
  border-radius: 20px;
  padding: 0.25rem 0.75rem;
  font-size: 0.8rem;
  font-weight: 500;
}

.pain-points-indicator.green { background-color: var(--green-color); }
.pain-points-indicator.amber { background-color: var(--amber-color); }
.pain-points-indicator.red { background-color: var(--red-color); }

.sub-card {
  background-color: var(--card-color);
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 4px 15px var(--shadow-color);
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.sub-process-icon {
  font-size: 2.5rem;
  color: var(--primary-color);
  margin-bottom: 1rem;
}

.sub-card h4 {
  font-size: 1.2rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
}

.sub-process-description {
  font-size: 0.9rem;
  color: #666;
  margin-bottom: 1rem;
}

.pain-points-indicator {
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: white;
  border-radius: 20px;
  padding: 0.25rem 0.75rem;
  font-size: 0.8rem;
  font-weight: 500;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background-color: var(--card-color);
  border-radius: 12px;
  padding: 2rem;
  width: 90%;
  max-width: 800px;
  max-height: 90vh;
  overflow-y: auto;
  position: relative;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.modal-close-icon {
  font-size: 2rem;
  color: #666;
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  transition: color 0.3s ease;
}

.modal-close-icon:hover {
  color: var(--primary-color);
}

.modal-content h2 {
  font-size: 1.8rem;
  margin-bottom: 1rem;
}

.modal-content h3 {
  font-size: 1.4rem;
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
}

.modal-content ul {
  list-style-type: none;
  padding-left: 1rem;
}

.modal-content li {
  margin-bottom: 0.5rem;
}

@media (max-width: 768px) {
  .modal-content {
    padding: 1.5rem;
    width: 95%;
  }

  .modal-content h2 {
    font-size: 1.5rem;
  }

  .modal-content h3 {
    font-size: 1.2rem;
  }
}

.pass-key-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 2000;
}

.pass-key-content {
  background-color: white;
  padding: 2rem;
  border-radius: 10px;
  width: 90%;
  max-width: 500px;
  text-align: center;
}

.pass-key-content h2 {
  margin-bottom: 1rem;
  color: #333;
}

.pass-key-content p {
  margin-bottom: 1.5rem;
  color: #666;
}

.pass-key-content form {
  display: flex;
  flex-direction: column;
}

.pass-key-content input {
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.pass-key-content button {
  padding: 0.5rem 1rem;
  background-color: #007AFF;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.pass-key-content button:hover {
  background-color: #0056b3;
}

